/*-- -------------------------- -->
<---          Contact           -->
<--- -------------------------- -*/

/* Mobile - 360px */
@media only screen and (min-width: 0rem) {
  #contact-1105 {
      padding: var(--sectionPadding);
      position: relative;
      z-index: 1;
      display: flex;
      justify-content: center;

  }
  #contact-1105 .cs-container {
      width: 100%;
      /* changes to 1280px at desktop */
      /* max-width: 36.5rem; */
      margin-left: 2rem;
      margin-right: 2rem;
      display: flex;
      justify-content: right;
      align-items: stretch;
      flex-direction: column;
      /* 48px - 80px */
      gap: 15rem;
  }
  #contact-1105 .cs-content {
      /* set text align to left if content needs to be left aligned */
      text-align: left;
      width: 100%;
      display: flex;
      flex-direction: column;
      /* centers content horizontally, set to flex-start to left align */
      align-items: flex-start;
  }

  #contact-1105 .cs-title {
      margin: 0 0 1rem 0;
  }
  #contact-1105 .cs-form {
      width: 100%;
      /* 24px - 48px top and bottom */
      /* 20px - 32px left and right */
      padding: clamp(1.5rem, 5.18vw, 3rem) clamp(1.25rem, 5.18vw, 2rem);
      /* prevents flexbox from affecting height and width */
      box-sizing: border-box;
      background-color: #f7f8f8;
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      align-items: center;
      gap: 0.75rem;
  }
  #contact-1105 .cs-label {
      /* 14px - 16px */
      font-size: clamp(0.875rem, 1.5vw, 1rem);
      width: 100%;
      color: var(--headerColor);
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: flex-start;
      gap: 0.25rem;
  }
  #contact-1105 .cs-input {
      font-size: 1rem;
      width: 100%;
      height: 3.5rem;
      padding: 0;
      padding-left: 1.5rem;
      color: var(--headerColor);
      background-color: #fff;
      border: none;
      /* prevents padding from adding to height and width */
      box-sizing: border-box;
  }
  #contact-1105 .cs-input::placeholder {
      color: #7d799c;
      opacity: 0.6;
  }
  #contact-1105 .cs-textarea {
      min-height: 7.5rem;
      padding-top: 1.5rem;
      margin-bottom: 0.75rem;
      font-family: inherit;
  }
  #contact-1105 .cs-button-solid {
    left: 15%;
      font-size: 1rem;
      /* 46px - 56px */
      line-height: clamp(2.875em, 5.5vw, 3.5em);
      text-decoration: none;
      font-weight: 700;
      text-align: center;
      margin: 0;
      color: #fff;
      border: none;
      min-width: 9.375rem;
      padding: 0 1.5rem;
      background-color: var(--primary);
      border-radius: 0.25rem;
      display: inline-block;
      position: relative;
      
      z-index: 1;
      /* prevents padding from adding to the width */
      box-sizing: border-box;
      transition: color 0.3s;
  }
  #contact-1105 .cs-button-solid:before {
      content: "";
      position: absolute;
      height: 100%;
      width: 0%;
      background: #000;
      opacity: 1;
      top: 0;
      left: 0;
      z-index: -1;
      border-radius: 0.25rem;
      transition: width 0.3s;
  }
  #contact-1105 .cs-button-solid:hover {
      color: #fff;
  }
  #contact-1105 .cs-button-solid:hover:before {
      width: 100%;
  }
  #contact-1105 .cs-submit {
      min-width: 17.6875rem;
      border-radius: 0;
  }
  #contact-1105 .cs-submit:hover {
      cursor: pointer;
  }
  #contact-1105 .cs-ul {
      width: auto;
      /* 40px - 92px, marign bottom and left are the same clamp values that the stripes box is absolutely positioned outside the cs-ul box */
      margin-bottom: clamp(2.5rem, 9vw, 5.75rem);
      /* 32px - 80px */
      margin-right: clamp(2rem, 9vw, 5rem);
      /* 40px - 100px top and bottom */
      /* 16px - 80px left and right */
      padding: clamp(2.5rem, 9.18vw, 6.25rem) clamp(1rem, 9.18vw, 5rem);
      box-sizing: border-box;
      background-color: #f7f7f7;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      /* 40px - 60px */
      gap: clamp(1.5rem, 6vw, 3.75rem);
      position: relative;
  }
  #contact-1105 .cs-ul:before {
      /* stripes graphic */
      content: "";
      height: 100%;
      width: 100%;
      background: url("https://csimg.nyc3.cdn.digitaloceanspaces.com/Icons/stripes-contact.svg");
      background-size: cover;
      background-position: center;
      background-repeat: no-repeat;
      opacity: 1;
      display: block;
      position: absolute;
      /* 40px - 92px */
      top: clamp(2.5rem, 9vw, 5.75rem);
      /* 32px - 80px */
      left: clamp(2rem, 9vw, 5rem);
      z-index: -1;
  }
  #contact-1105 .cs-li {
      list-style: none;
      display: flex;
      justify-content: flex-start;
      align-items: flex-start;
      /* 12px - 20px */
      gap: clamp(0.75rem, 2vw, 1.25rem);
  }
  #contact-1105 .cs-li:hover .cs-icon-wrapper {
      transform: scale(1.1);
  }
  #contact-1105 .cs-header {
      /* 20px - 25px */
      font-size: clamp(1.25rem, 2.5vw, 1.5625rem);
      font-weight: 700;
      line-height: 1.2em;
      margin-bottom: 0.5rem;
      color: var(--headerColor);
      display: block;
  }
  #contact-1105 .cs-link {
      /* 14px - 16px */
      font-size: clamp(0.875rem, 2vw, 1rem);
      line-height: 1.5em;
      text-decoration: none;
      color: #767676;
      display: block;
      position: relative;
  }
  #contact-1105 .cs-link:hover {
      text-decoration: underline;
  }
  #contact-1105 .cs-icon-wrapper {
      /* 44px - 80px */
      width: clamp(2.75rem, 8.3vw, 5rem);
      height: clamp(2.75rem, 8.3vw, 5rem);
      margin: 0;
      border-radius: 50%;
      border: 1px solid #bababa;
      display: flex;
      justify-content: center;
      align-items: center;
      /* prevents flexbox from squishing it */
      flex: none;
      transition: transform 0.3s;
  }
  #contact-1105 .cs-icon {
      /* 20px - 40px */
      width: clamp(1.25rem, 3vw, 2.5rem);
      height: auto;
      display: block;
  }
  #contact-1105 .cs-picture {
      width: 100%;
      border-radius: 0.5rem;
      /* clips image corners */
      overflow: hidden;
      display: block;
      position: relative;
      /* width divided height */
      aspect-ratio: 1.44;
  }
  #contact-1105 .cs-img {
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
      object-fit: cover;
  }
}
/* Desktop - 1024px */
@media only screen and (min-width: 64rem) {
  #contact-1105 .cs-container {
      max-width: 80rem;
      flex-direction: row;
      justify-content: flex-start;
      align-items: flex-start;
  }
  #contact-1105 .cs-content {
      max-width: 33.875rem;
      /*sends it to the right in the 2nd position */
      order: 2;
  }
  #contact-1105 .cs-ul {
      max-width: 32.0625rem;
  }
}